html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

html,
body {
	height: 100%;
	position: relative;
	font-family: 'Montserrat', sans-serif;
	font-size: 16px;
	color: $baseClr;
}

button:focus,
input:focus {
	outline: transparent;
}
ul,
li {
	margin: 0;
}
li {
	list-style: none;
}
a {
	text-decoration: none;
}

article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
.wrapper {
	max-width: 1200px;
	padding: 0 15px;
	margin: 0 auto;
}