.footer {
    position: relative;
    background: url("../img/footer-bg.jpg")center no-repeat;
    background-size: cover;
    margin-top: 30px;
    padding: 20px 0;
    width: 100%;
    &:after {
        @include pos-c;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.3);
        
    }
    &__inner {
        @include flex-b;
        align-items: start;
        position: relative;
        z-index: 1;
        @media (max-width: 840px) {
            flex-wrap: wrap;
        }
    }
    &__col {
        width: 35%;
        &:last-child {
            width: 20%;
            @media (max-width: 1140px) {
                width: 30%;
            }
            @media (max-width: 840px) {
                width: 40%;
            }
            @media (max-width: 550px) {
                width: 60%;
            }
            @media (max-width: 420px) {
                width: 70%;
            }
        }
        @media (max-width: 840px) {
            width: 50%;
        }
        @media (max-width: 420px) {
            width: 100%;
        }
    }
    h3 {
        text-transform: uppercase;
        font-family: "Romul", sans-serif;
        font-weight: 400;
        color: #ffffff;
        font-size: 18.75px;
        position: relative;
        padding-bottom: 15px;
        margin: 20px 0 30px 0;
        &:after {
            @include pos;
            top: 100%;
            left: 0;
            left: 0;
            width: 70px;
            height: 1px;
            background: $beige;
        }
    }
    &__list {
        padding: 0;
        margin-bottom: 30px;
        position: relative;
        &:after {
            @include pos;
            left: 75%;
            top: 50%;
            transform: translateY(-50%);
            width: 1px;
            height: 100%;
            background: #888889;
            @media (max-width: 1024px) {
                display: none;
            }
        }
        li {
            margin-bottom: 7px;
        }
    }
    &__link {
        display: block;
        line-height: 120%;
        color: #a6a5a4;
        font-weight: 400;
        font-size: 15px;
        transition: all .4s ease;
        text-transform: capitalize;
        &:hover {
            color: #ffffff;
        }
    }
    P {
        font-weight: 400;
        font-size: 15px;
        color: #a6a5a4;
        line-height: 130%;
    }
    &__order,
    &__tarif {
        text-transform: uppercase;
        text-align: center;
        font-size: 16px;
        font-weight: 500;
        display: block;
        padding-bottom: 7px;
        margin-bottom: 15px;
    }
    &__order {
        margin-top: 20px;
        color: #ffffff;
        border-bottom: 2px solid #ffffff;
    }
    &__tarif {
        border-bottom: 2px solid #a6a5a4;;
        color: #a6a5a4;
        transition: all .3s ease;
        &:hover {
            color: #ffffff;
            border-bottom: 2px solid #ffffff;
        }
    }
    &__contacts {
        background: rgba(166,165,164, 0.2);
        padding: 10px 15px;
        .contacts {
            position: relative;
            z-index: 1; 
            text-align: right;
            &__link {
                color: #ffffff;
                margin: 0 0 10px;
                letter-spacing: 0;
                span {
                    margin-top: 5px;
                    color: #9a9a9a;
                    letter-spacing: 0;
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

}
.social {
    &__list {
        @include flex-b;
    }
    &__link {
        display: block;
    }
    &__icon {
        display: inline-block;
        width: 28px;
        height: 28px;
        fill: #9a9a9a;
        transition: fill .3s ease;
        &:hover {
            fill: $beige;
        }
    }
   
}