.header-sticky {
    box-shadow: 0 6px 20px rgba(0,0,0,.05);
    position: fixed;
    transition: all .6s ease;
    left: 0;
    right: 0;
    z-index: 999;
    .header__top-inner {
       margin-top: -90px;
       visibility: hidden;
    }
}

.header {
    .wrapper.bordered {
        position: relative;
        &:after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 45%;
            height: 1px;
            background: $baseClr;
        }
        &:before {
            position: absolute;
            content: '';
            top: 0;
            right: 0;
            width: 45%;
            height: 1px;
            background: $baseClr;
        }
    }
    .wrapper {
        position: relative;
    }
    &__top-inner {
        @include flex-b;
        position: relative;
        margin: 25px 0 10px 0; 
        transition: all .3s ease;
        z-index: 100;
        
    }
    &__contacts {
        width: 40%;
        @include flex-s;
    }
    &__buttons {
        width: 40%;
        @include flex-s;
    }
    &__logo {
        @include pos-c;
        top: 40%;
        text-align: center;
        @media (max-width: 768px) {
           .logo img {
              width: 100%; 
           } 
        }
    }
    &__bottom-block {
        background: $baseClr;
    }
    .mobile__icon {
        width: 0;
        height: 0;
        @media (max-width: 1130px) {
            display: inline-block;
            width: 24px;
            height: 24px;
            fill: $beige;
        }
    }
   
}
.contacts {
    &__link {
        display: block;
        text-transform: uppercase;
        color: $baseClr;
        font-size: 16px;
        font-weight: 500;
        letter-spacing: 1px;
        margin: 15px 45px 0 0;
        &:last-child {
            margin-right: 0;
        }
        span {
            margin-top: 10px;
            position: relative;
            display: block;
            color: $beige;
            letter-spacing: 0.7px;
            font-size: 10px;
            &:after{
                @include pos-t-c;
                right: 0px;
                width: 40px;
                height: 1px;
                background: $beige;
            }
        }
    }
}
.header .contacts {
    &__link,
    &__link span {
        @media (max-width: 1130px) {
            display: none;
        }
    }
}
.buttons {
    margin: 30px 0 20px 0;  
    .btn-sm {
        margin: 0  0 0 20px;  
    }
    .btn-bordered {
        border-width: 1px;
    }
    @media (max-width: 1130px) {
        .btn {
            display: none;
        }
    }
}
.menu {
    padding: 16px 0;
    &__list {
        padding: 0 15px;
        margin: 0 -15px;
        @include flex-b;
        @media (max-width: 1024px) {
        display: none;
        }
        li {
            list-style: none;
            line-height: 25px;
            position: relative;
            width: 100%;
            text-align: center;
            &:after {
                @include pos-t-c;
                width: 2px;
                height: 100%;
                left: 0;
                background: #737075;
                @media (max-width: 1024px) {
                    display: none;
                }
            }
            &:first-child {
                text-align: left;
                &:after {
                    display: none;
                }
            }
            &:last-child {
                text-align: right;
            }
        }
    }
    &__link {
        text-transform: uppercase;
        color: #ffffff;
        font-family: "Romul", sans-serif;
        font-size: 14px;
        transition: color .3s ease;
        &:hover {
            color: $beige
        }
    }
    @media (max-width: 1024px) {
        &__icon {
            position: relative;
            width: 22px;
            height: 20px;
            margin-left: auto;
            z-index: 100;
            transform: rotate(0deg);
            cursor: pointer;
            span {
                position: absolute;
                width: 100%;
                height: 2px;
                background:$beige;
                display: block;
                transform: rotate(0deg);
                &:nth-child(1) {
                    top: 5px;
                }
                &:nth-child(2) {
                    top: 12px;
                }
                &:nth-child(3) {
                    top: 19px;
                }
            }
        }
        
    }

}

.menu-open {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background: $baseClr;
    z-index: 10;
    padding-top: 50px;
    margin: 0;
    li {
        text-align: center!important;
    }
    
}