$beige: #d0aa8c;
$baseClr: #2f2c34;


p {
    font-size: 12.5px;
}
.btn {
    height: 43px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    padding: 0px 80px;
    text-transform: uppercase;
    color: $baseClr;
    font-size: 13px;
    font-weight: 500;
    letter-spacing: 2px;
    transition: all 0.4s ease;
    @media (max-width: 768px) {
        padding: 0px 50px;
    }
}
.btn-sm {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    letter-spacing: 1px;
    height: 30px;
    padding: 0px 30px;
}
.btn-bordered {
    border: 2px solid $baseClr;
    background: #ffffff;
    &:hover {
        background: $beige;
        color: #ffffff;
        border: 2px solid $beige;
    }
}
.btn-beige {
    background: $beige;
    color: #ffffff;
    border: 1px solid $beige;
    &:hover {
        border: 1px solid $baseClr;
        background: #ffffff;
        color: $baseClr;
    }
}

.breadcrumbs {
    margin-bottom: 30px;
    &__list {
        @include flex-c;
        border-bottom: 1px solid #e8e8e9;
        padding: 10px 0;
        li {
            position: relative;
            padding: 0 10px;
            line-height: 10px;
            border-left: 2px solid #a9a8aa;
            &.active {
                border-left: 2px solid $beige;
                .breadcrumbs__link {
                    color: $beige;
                }
            }
            &:first-child {
                border-left: transparent;
            }
        }
    }
    &__link {
        display: block;
        color: $baseClr;
        font-size: 10.42px;
        text-transform: uppercase;
        font-weight: 500;
    }
}
 
 
  