.main {
    overflow: hidden;
    &__top-inner {
        text-align: center;
        margin: 0px auto;
        max-width: 730px;
        h4 {
            font-size: 1rem;
            text-transform: uppercase;
            line-height: 100%;
            font-weight: 400;
            margin: 15px 0 10px;
            letter-spacing: 1.3px;
        }
        h1 {
            font-weight: 500;
            font-size: 2.34375rem;
            line-height: 100%;
            padding: 0px 20px;
            margin: 0 0 22px 0;
            font-family: "Romul", sans-serif;
            position: relative;
            display: inline-block;
            &:after,
            &:before {
                @include pos-t-c;
                width: 80px;
                height: 2px;
                background: $beige;
                @media (max-width: 640px) {
                    margin-top: 15px;
                    top: 100%;
                    left: 50%;
                    transform: translateX(-50%);
                }
            }
            &:after {
                left: 100%; 
                @media (max-width: 640px) {
                    display: none;
                }
            }
            &:before {
                right: 100%;
               
            }

        }
    }
    p {
        line-height: 16.67px;
    }
}

.cards__wrapper {
    @include flex-b;
    flex-wrap: wrap;
    align-items: start;
    margin: 0 -30px;
    .card__block {
       padding: 20px 30px; 
       width: 50%;
       @media (max-width: 1024px) {
        width: 70%;
        }
        @media (max-width: 768px) {
            width: 80%;
            }
        @media (max-width: 620px) {
            width: 100%;
            }
    }
    @media (max-width: 1024px) {
       justify-content: center;
    }
}

.card {
    text-align: center;
    &__img {
        display: block;
        width: 100%;
        height: 235px;
        overflow: hidden; 
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
            transition: transform 0.5s linear;
            transform: scale(1);
        }
        &:hover img {
            transform: scale(1.2);
       
        }
    }
    h2 {
        text-transform: uppercase;
        color: $baseClr;
        font-size: 25px;
        font-weight: 400;
        padding-bottom: 25px;
        margin-bottom: 20px;
        letter-spacing: 2px;
        position: relative;
        &:after {
            @include pos;
            top: 100%;
            left: 50%;
            transform: translateX(-50%);
            width: 90px;
            height: 2px;
            background: $beige;
        }
    }
    .btn {
        margin-top: 10px;
    }
}